import { Component, OnInit } from '@angular/core';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
//对账管理
@Component({
  selector: 'app-reconciliation-manage',
  templateUrl: './reconciliation-manage.component.html',
  styleUrls: ['./reconciliation-manage.component.scss'],
	providers: [Commfunc]
})
export class ReconciliationManageComponent implements OnInit {

  items1: MenuItem[];
  items2: MenuItem[];
  items3: MenuItem[];
  show:number;
  constructor(public commfunc:Commfunc) { }

  ngOnInit() {
    this.items1=[
      {
        label: '手工对账',
        routerLink:'/pages/tzb/order/order-center/reconciliation-manage/manual-reconciliation'
      }
    ];
    this.items2=[
      {
        label: '对账结果查询',
        routerLink:'/pages/tzb/order/order-center/reconciliation-manage/reconciliation-result'
      }
    ];
    this.items3=[
      {
        label: '对账异常处理',
        routerLink:'/pages/tzb/order/order-center/reconciliation-manage/reconciliation-exception'
      }
    ]
 this.judgStatu()
  }
  judgStatu() {
    let url = window.location.href;
    let urlSplit = url.split('/');
    urlSplit.map(v => {
      if (v === 'manual-reconciliation') {
        this.show = 1;
      } else if (v === 'reconciliation-result') {
        this.show = 2;
      }else if (v === 'reconciliation-exception') {
        this.show = 3;
      }
    })
  }
  apply(num) {
    this.show = num;
  }
  //按钮权限
	permissionCheck(id){
		return	this.commfunc.permissionCheck(id)
	  }

}
