import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ReconResultBean } from './bean/reconciliation-result.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as moment from "moment";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';

//对账结果查询
@Component({
  selector: 'app-reconciliation-result',
  templateUrl: './reconciliation-result.component.html',
  styleUrls: ['./reconciliation-result.component.scss'],
  providers: [ConfirmationService]
})

export class ReconciliationResultComponent implements OnInit {
  //标题
  title: string = '对账结果查询';
  //表数据
  tableData: any = [];
  //对账系统
  system: any = [];
  //交易时间
  checkDate: Date = new Date();
  ch: any = CALENDAR_CN;//日历英文转化为中文
  //bean
  reconResultBean: ReconResultBean = new ReconResultBean();
  //提示信息
  msgs: Message[] = [];
  //校验
  userform: FormGroup;
  //分页
  total: string; //总页数
  first: number = 0;
  //对账系统下拉框
  option: any = [];
  //点击重置按钮，验证信息隐藏
  flag: any = true;
  constructor(
    public fb: FormBuilder,
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {
    this.reconResultBean.mainParameter.sheetOnePage = 20;
    this.reconResultBean.mainParameter.pageNumber = 1;
  }


  ngOnInit() {
    this.reconResultBean.mainParameter.checkingSystemId = 'BKCE_S';
    //表单校验
    this.userform = this.fb.group({
      'checkingSystemId': new FormControl('', Validators.required),
      'checkDate': new FormControl('', Validators.required)
    });

    //对账系统下拉框
    this.orderHttpService.queryCheckingSystemService({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.option = this.commfunc.toChangeTree(data.outList, "dataSourceName", "dataSource");
        this.option.unshift({ label: '请选择', value: '' })
      } else {
        this.msgs = [];
        this.msgs.push({
          severity: 'error', summary: '提示', detail: data.returnCode.message
        });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }

  queryClick() {
    this.first = 0;
    this.reconResultBean.mainParameter.pageNumber = 1;
    this.query();
  }

  //查询按钮
  query() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //点击查询，验证不通过出现提示信息
    this.flag = true;
    // //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }

    //提交服务
    this.reconResultBean.mainParameter.checkingDate = this.commfunc.transDateN(this.checkDate);
    this.orderHttpService.queryCheckingGroupService(this.reconResultBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.outList;
        this.total = data.totalCount;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

  }

  //再次对账
  detailClick(item) {
    this.reconResultBean.mainParameter.dataSource = item.dataSource;
    this.reconResultBean.mainParameter.checkingBeginDate = item.checkingDate;
    // this.reconResultBean.mainParameter = { dataSource: item.dataSource, checkingBeginDate: item.checkingDate }
    this.orderHttpService.requestCheckingService(this.reconResultBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.reconResultBean.mainParameter.sheetOnePage = event.rows * 1;
    //当前页
    let currentPage = event.page + 1;
    this.reconResultBean.mainParameter.pageNumber = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置
  resetClick() {
    this.reconResultBean = new ReconResultBean();
    this.reconResultBean.mainParameter.checkingSystemId = '';
    this.checkDate = null;
    this.reconResultBean.mainParameter.sheetOnePage = 20;
    this.reconResultBean.mainParameter.pageNumber = 1;
    //点击查询，验证不通过出现提示信息
    this.flag = false;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
