import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { OrderHttpService } from './../../../../http/order.http.service';
import { Message } from 'primeng/primeng';
import { ReconExceptionBean } from './../bean/reconciliation-exception.bean';
// 详情
@Component({
    selector: 'exception-detail',
    styleUrls: ['exception-detail.scss'],
    templateUrl: 'exception-detail.html'
})

export class ExceptionDetail implements OnChanges {
    @Input() inValue: string;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //校验
     userform: FormGroup;
    //提示信息
     msgs: Message[] = [];
    //实体
     reconExceptionBean: ReconExceptionBean = new ReconExceptionBean();
    //返回的数据
     list: any = [];

    constructor(
        public fb: FormBuilder,
        public orderHttpService: OrderHttpService
    ) {

    }

    ngOnInit() {

    }

    ngOnChanges() {
        //详情接口
        let param = {orderNo: this.inValue};
        this.orderHttpService.queryOrderDetail(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if(data.orderDetailList[0]){
                    this.list = data.orderDetailList[0];
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        });
    }

    //确定按钮进行关闭
    addCloseBtn() {
        this.outValue.emit(false);
    }
}