import { Component, OnInit } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ReconExceptionBean } from './bean/reconciliation-exception.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as moment from "moment";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';

//对账异常处理
@Component({
  selector: 'app-reconciliation-exception',
  templateUrl: './reconciliation-exception.component.html',
  styleUrls: ['./reconciliation-exception.component.scss'],
  providers: [ConfirmationService, Commfunc]
})

export class ReconciliationExceptionComponent implements OnInit {
  //表数据
  tableData: any = [];
  //对账系统下拉框
  reconSysOption: any = [];
  //状态
  status: any = [];
  //对账日期
  ch: any = CALENDAR_CN;
  // public reconDate:any;
  reconDate: any = new Date();
  //bean
  reconExceptionBean: ReconExceptionBean = new ReconExceptionBean();
  //模态框
  headerTitle: string = "详情"; //模态框的标题
  display: boolean = false;//模态框是否显示
  showModel: string;
  //修改的参数
  updateValue: any = [];
  //分页
  total: string; //总页数
  first: number = 0;
  //提示信息
  msgs: Message[] = [];
  userform: FormGroup; //校验
  //点击重置后，隐藏信息提示
  flag: boolean = false;
  code: any;
  constructor(
    public commfunc: Commfunc,
    public orderHttpService: OrderHttpService,
    public fb: FormBuilder,

  ) {
    this.reconExceptionBean.pageNum = 1;
    this.reconExceptionBean.pageSize = 10;

    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.status = this.code['status'];
  }

  ngOnInit() {
    //对账系统初始化
    this.reconExceptionBean.checkingSys = 'BKCE_S'
    this.reconExceptionBean.statusId = '1'
    //调查询
    this.query();
    this.userform = this.fb.group({
      'checkingSys': new FormControl('', [Validators.required]),
      // 'statusId': new FormControl('', [Validators.required]),
      'reconDate': new FormControl('', [Validators.required]),
    });

    //对账系统下拉框
    this.orderHttpService.queryCheckingSystemService({}).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.reconSysOption = this.commfunc.toChangeTree(data.outList, "dataSourceName", "dataSource");
        this.reconSysOption.unshift({ value: '', label: "请选择" });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })

    //按回车键查询
    let _thiss = this;
    document.onkeydown = function (event) {
      if (event && event.keyCode === 13) {
        _thiss.queryClick();
      }
    }
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  queryClick() {

    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    this.flag = true;
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return
      }
    }
    this.first = 0;
    this.reconExceptionBean.pageNum = 1;
    this.query();
  }

  //查询按钮
  query() {
    if (this.reconDate) {
      this.reconExceptionBean.checkingDate = this.commfunc.transDateN(this.reconDate);
    }
    this.orderHttpService.adjustChecking(this.reconExceptionBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.outList;
        this.total = data.totalCount + '';
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //详情按钮事件
  detailClick(item) {
    this.headerTitle = '详情';
    this.display = true;
    this.showModel = '1';
    this.updateValue = item.orderId;
  }

  //详情的回调
  detailCall(param) {
    this.display = param;
    this.query();
  }

  //处理按钮事件
  handleClick(item) {
    let params = { orderId: item.orderId }
    this.orderHttpService.excOrderProcess(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }


  //分页
  paginate(event) {
    //每页显示的条数
    this.reconExceptionBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.reconExceptionBean.pageNum = currentPage;
    this.first = event.page * event.rows;
    //调用查询的方法
    this.query();
  }

  //重置按钮
  resetClick() {
    this.flag = false;
    this.reconExceptionBean = new ReconExceptionBean();
    this.reconDate = new Date();
    this.reconExceptionBean.pageNum = 1;
    this.reconExceptionBean.pageSize = 10;
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
