import { Component, OnInit, OnChanges } from '@angular/core';
import { OrderHttpService } from './../../../http/order.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { ManualReconBean } from './bean/manual-reconciliation.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as moment from "moment";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';

//手工对账
@Component({
  selector: 'app-manual-reconciliation',
  templateUrl: './manual-reconciliation.component.html',
  styleUrls: ['./manual-reconciliation.component.scss'],
  providers: [ConfirmationService]
})
export class ManualReconciliationComponent implements OnInit {
  //对账系统
  option: any = [];
  //bean
  manualReconBean: ManualReconBean = new ManualReconBean();
  //提示信息
  msgs: Message[] = [];
  //校验
  userform: FormGroup;
  //日期
  ch: any = CALENDAR_CN;
  //交易时间
  dealDate: Date = new Date();
  //点击重置按钮，验证信息隐藏
  flag: any = true;
  //弹窗
  display: boolean = true;
  constructor(
    public fb: FormBuilder,
    public orderHttpService: OrderHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc
  ) {

  }

  ngOnInit() {
    //对账系统初始化
    this.manualReconBean.mainParameter.dataSource = "BKCE_S";
    //表单校验
    this.userform = this.fb.group({
      'dataSource': new FormControl('', Validators.required),
      'dealDate': new FormControl('', Validators.required)
    });
    // 对账系统下拉框
    this.orderHttpService.queryCheckingSystemService(this.manualReconBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.option = this.commfunc.toChangeTree(data.outList, "dataSourceName", "dataSource");
        this.option.unshift({ value: '', label: '请选择' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
    
  }


  //对账按钮
  queryClick() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //点击查询，验证不通过出现提示信息
    this.flag = true;
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }


    //提交服务
    this.manualReconBean.mainParameter.checkingBeginDate = this.commfunc.transDateN(this.dealDate);
    this.orderHttpService.requestCheckingService(this.manualReconBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail:'对账成功，请查询对账结果' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //重置
  resetClick() {
    this.manualReconBean = new ManualReconBean();
    this.manualReconBean.mainParameter.dataSource = "";
    this.dealDate = null;
    //点击重置按钮，信息提示消失
    this.flag = false;
  }

}
