import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
//页面
import { ReconciliationManageComponent} from './reconciliation-manage.component';//对账管理
import { ManualReconciliationComponent } from './manual-reconciliation/manual-reconciliation.component';//手工对账
import { ReconciliationResultComponent } from './reconciliation-result/reconciliation-result.component';//对账结果查询
import { ReconciliationExceptionComponent } from './reconciliation-exception/reconciliation-exception.component';//对账异常处理

const routes: Routes = [
    {
        path: '',
        component: ReconciliationManageComponent,
        children: [
            { path: '', redirectTo: 'manual-reconciliation', pathMatch: 'full' },
            {
                path: 'manual-reconciliation', component: ManualReconciliationComponent,
            },
            {
                path: 'reconciliation-result', component: ReconciliationResultComponent,
            },
            {
                path: 'reconciliation-exception', component: ReconciliationExceptionComponent,
            },
        ]
    },
];

export const ReconManageRouting = RouterModule.forChild(routes);